import {Header, Divider, Table, Icon} from 'semantic-ui-react'

export default ({
  description,
  audio
}) => (
  <React.Fragment>
    <Header as='h3'>About this personality</Header>
    <p>{description}</p>

    <Divider />

    <Table celled>
      <Table.Header>
        <Table.Row>
          <Table.HeaderCell colSpan='2'>Personality Sample</Table.HeaderCell>
        </Table.Row>
      </Table.Header>

      <Table.Body>
        <Table.Row>
          {/* <Table.Cell><i class="play circle icon"></i></Table.Cell> */}
          <Table.Cell>Audio</Table.Cell>
          <Table.Cell></Table.Cell>
        </Table.Row>
      </Table.Body>
    </Table>
  </React.Fragment>
)


